.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background: linear-gradient(to bottom, #333, #222);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
}

.container img {
    width: 100%;
    max-width: 500px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
}

.container h1 {
    font-size: 36px;
    color: #fff;
    margin-bottom: 10px;
}

.container li {
    font-size: 18px;
    color: #fff;
    margin-bottom: 10px;
}

.container p {
    font-size: 24px;
    color: #fff;
    margin-top: 20px;
}

.detail {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.detail img {
    width: 100%;
    max-width: 600px;
    height: auto;
    margin-bottom: 20px;
}

.detail h1 {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 10px;
}

.detail ul {
    margin-bottom: 20px;
}

.detail p {
    line-height: 1.5;
    min-height: 150px;
    /* 최소한의 높이 */
}