.title {
    font-size: 24px;
    font-weight: bold;
    text-align: left;
    margin-left: 20px;
    background-color: #F5F5F5;
    padding: 10px;
    color: #090707;
}

.container {
    background-color: #F5F5F5;
    padding: 20px;
}

.movie-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    justify-items: center;
}

.movie-card {
    width: 300px;
    height: 450px;
    background-color: #E0E0E0;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.16);
    overflow: hidden;
    position: relative;
    transition: transform 0.3s ease-in-out;
    cursor: pointer;
}

.movie-card:hover {
    transform: translateY(-10px);
}

.summary {
    font-size: 14px;
    color: #ddd;
    line-height: 1.5;
    margin-top: 10px;
}